<template>
  <div class="build-progress section">
    <div class="section__header">
      <div class="top-list">
        <div v-for="it in topItems" :key="it.name" class="top-list__item row">
          <div class="name">{{ it.name }}：</div>
          <div class="value">{{ it.value }}</div>
        </div>
      </div>
    </div>
    <div class="section__body">
      <div class="list">
        <div v-for="it in listItems" :key="it.name" class="list__item">
          <div class="wrapper">
            <div class="name">{{ it.name }}</div>
            <div class="value">{{ it.value }}</div>
          </div>
        </div>
        <FlashSpin class="spin" />
      </div>
    </div>
  </div>
</template>

<script>
import FlashSpin from "@/components/widgets/FlashSpin.vue";
import { dashboardMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardMixin],

  components: {
    FlashSpin,
  },

  props: ["data", "type"],

  data() {
    return {};
  },

  computed: {
    topItems() {
      const type = this.type;
      const data = this.data || {};

      let items = [];

      if (this.isMonthly) {
        if (type == "company") {
          items = [{ name: `本月新完成企业`, value: data.completedCompany }];
        } else if (type == "system") {
          items = [{ name: `本月新完成系统`, value: data.completedSystem }];
        }
      } else if (this.isAnnual) {
        if (type == "company") {
          items = [
            { name: `本年完成企业`, value: data.completedCompany },
            { name: `本年参与建设企业`, value: data.buildingCompany },
          ];
        } else if (type == "system") {
          items = [
            { name: `本年完成系统`, value: data.completedSystem },
            { name: `本年参与建设系统`, value: data.buildingSystem },
          ];
        }
      }

      return items;
    },

    listItems() {
      const data = this.data || {};
      return [
        { name: "深度融合", value: data.deepIntegrated },
        { name: "对接测试", value: data.testing },
        { name: "主数据集成", value: data.masterDataIntegrated },
        { name: "计划准备", value: data.planned },
      ];
    },
  },

  mounted() {},
};
</script>

