<template>
  <BlockSection height="325px">
    <DataTray :data="data" height="100%" :title="innerTitle" />
  </BlockSection>
</template>

<script>
import { dashboardBlockMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardBlockMixin("masterDataPushTotal")],

  data() {
    return {};
  },

  computed: {
    innerTitle() {
      let prefix = "";

      if (this.isMonthly) {
        prefix = "本月";
      } else if (this.isAnnual) {
        prefix = "本年";
      }

      return `${prefix}主数据推送数量`;
    },
  },

  mounted() {},
};
</script>

<style lang="less" scoped></style>
