<template>
  <BlockSection height="310px" title="主数据集成情况" theme="2">
    <div class="section__body row" style="padding: 0 10px">
      <div class="chart flex-main">
        <div class="outer-chart">
          <div ref="chart" class="inner-chart"></div>
        </div>

        <div class="chart__legend row">
          <div v-for="it in legends" :Key="it.name" class="item row">
            <div class="icon" :style="`background: ${it.color}`">&nbsp;</div>
            <div class="label">{{ it.name }}</div>
          </div>
        </div>
      </div>
    </div>
  </BlockSection>
</template>

<script>
import { graphic } from "echarts";
import { dashboardBlockMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardBlockMixin("masterDataIntegration")],

  data() {
    return {};
  },

  computed: {
    legends() {
      return [
        { name: "昨日新增", color: "#b63327" },
        { name: "同比增长", color: "#ff7607" },
        { name: "环比增长", color: "#af1bec" },
        { name: "总数", color: "#0167e9" },
      ];
    },

    chartOptions() {
      const thiz = this;
      const labelIcon = "/pages/dashboard/reports/d_6.png";

      // 由大到小排列（倒序）
      const data = (this.data.list || []).map((it) => {
        return [it.name, it.total, it.incY, it.incT, it.incH];
      });

      const subItem = (prop, opt) => {
        return {
          type: "bar",
          stack: "subitem",
          barWidth: 12,
          label: {
            show: true,
            position: "insideRight",
            formatter: (p) => {
              const percent = thiz.$percentage(p.data[3] / p.data[1]);
              return `${percent}%`;
            },
            textStyle: {
              color: "#ffffff",
              fontWeight: "bold",
              textAlign: "right",
            },
          },
          itemStyle: {
            color: new graphic.LinearGradient(0, 0, 1, 0, [
              { offset: 0, color: opt.color1 },
              { offset: 1, color: opt.color2 },
            ]),
          },
          encode: { x: prop },
        };
      };

      return {
        dataset: {
          source: [["name", "total", "yestoday", "tongbi", "huanbi"], ...data],
        },
        tooltip: { show: true },
        grid: {
          left: 0,
          right: 10,
          top: 25,
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
          axisLine: { show: false },
          axisLabel: { show: false },
          splitLine: { show: false },
        },
        yAxis: {
          type: "category",
          axisTick: { show: false },
          axisLabel: {
            show: true,
            margin: -5,
            formatter: (v) => {
              return v + "{icon|}";
            },
            textStyle: {
              fontWeight: "bold",
              color: "#ffffff",
              fontSize: 16,
            },
            rich: {
              icon: {
                height: 30,
                width: 30,
                padding: [0, 0, 0, 0],
                backgroundColor: { image: labelIcon },
              },
            },
          },
          axisLine: { show: false },
          splitLine: { show: false },
        },
        series: [
          subItem("yestoday", { color1: "#b63327", color2: "#ea4c5f" }),
          subItem("tongbi", { color1: "#ff7607", color2: "#fec180" }),
          subItem("huanbi", { color1: "#af1bec", color2: "#d7a2ee" }),
          {
            type: "bar",
            barWidth: 12,
            label: {
              show: true,
              position: "insideRight",
              textStyle: {
                color: "#ffffff",
                fontWeight: "bold",
                textAlign: "right",
              },
            },
            itemStyle: {
              color: new graphic.LinearGradient(0, 0, 1, 0, [
                { offset: 0, color: "#0167e9" },
                { offset: 1, color: "#12abeb" },
              ]),
            },
            encode: { x: "total" },
          },
        ],
      };
    },
  },
};
</script>

<style lang="less" scoped>
.chart {
  .inner-chart {
    width: 450px;
    height: 220px;
  }

  &__legend {
    position: absolute;
    z-index: 10;
    top: 55px;
    left: 60px;
    color: white;

    align-items: center;

    .icon {
      width: 21px;
      height: 9px;
      margin-top: 5px;
      border-radius: 2px;
    }

    .label {
      padding-left: 10px;
      color: #3752aa;
      font-weight: bold;
      width: 80px;
    }
  }
}
</style>
