<template>
  <BlockSection height="380px" title="业财数据集成情况" theme="2">
    <div class="section__body row" style="padding: 0 10px">
      <div class="chart flex-main">
        <div class="outer-chart">
          <div ref="chart" class="inner-chart"></div>
        </div>

        <div class="chart__legend row">
          <div v-for="it in legends" :Key="it.name" class="item row">
            <div class="icon" :style="`background: ${it.color}`">&nbsp;</div>
            <div class="label" :style="`color: ${it.color}`">{{ it.name }}</div>
          </div>
        </div>
      </div>
    </div>
  </BlockSection>
</template>

<script>
import { graphic } from "echarts";
import { dashboardBlockMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardBlockMixin("bizFinBillIntegration")],

  data() {
    return {};
  },

  computed: {
    legends() {
      return [
        { name: "同比增长", color: "#02dbce" },
        { name: "环比增长", color: "#7fa848" },
        { name: "总数", color: "#3c4bdf" },
      ];
    },

    chartOptions() {
      // 由大到小排列（倒序）
      const data = this.data.list || [];

      const nameData = data.map((it) => it.name);
      const totalData = data.map((it) => it.total);
      const tbData = data.map((it) => it.incT);
      const hbData = data.map((it) => it.incH);

      return {
        tooltip: {
          show: true,
        },
        grid: {
          left: 0,
          right: 30,
          top: 30,
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          axisLabel: {
            color: "#ffffff",
            fontSize: 10,
          },
          axisLine: {
            show: true,
            lineStyle: { color: "rgba(83, 118, 195, 0.5)" },
          },
          data: nameData,
        },
        yAxis: {
          min: 0,
          max: "dataMax",
          axisLabel: {
            color: "#ffffff",
          },
          axisLine: {
            show: true,
            lineStyle: { color: "#5376c3" },
          },
          splitLine: {
            show: true,
            interval: 100,
            lineStyle: { color: "rgba(83, 118, 195, 0.5)" },
          },
        },
        series: [
          {
            type: "bar",
            barWidth: 10,
            data: tbData,
            itemStyle: {
              barBorderRadius: [2, 2, 0, 0],
              color: new graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: "#03faca" },
                { offset: 1, color: "#01b3d7" },
              ]),
            },
          },
          {
            type: "bar",
            barWidth: 10,
            data: hbData,
            itemStyle: {
              barBorderRadius: [2, 2, 0, 0],
              color: new graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: "#e49a11" },
                { offset: 1, color: "#0fb884" },
              ]),
            },
          },
          {
            type: "bar",
            barWidth: 10,
            data: totalData,
            itemStyle: {
              barBorderRadius: [2, 2, 0, 0],
              color: new graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: "#0597e5" },
                { offset: 1, color: "#6a0ad9" },
              ]),
            },
          },
        ],
      };
    },
  },
};
</script>

