    align-items: center;

    .icon {
      width: 21px;
      height: 9px;
      margin-top: 5px;
      border-radius: 2px;
    }

    .label {
      padding-left: 10px;
      color: #3752aa;
      font-weight: bold;
      width: 80px;
    }
  }
}
</style>
