<template>
  <BlockSection height="275px">
    <div class="section__header row">
      <div class="title">
        <div class="name">{{ innerTitle }}</div>
        <div class="value">{{ data.total }}</div>
      </div>
      <div class="chart flex-main">
        <div class="outer-chart">
          <div ref="chart" class="inner-chart"></div>
        </div>
      </div>
    </div>
    <div class="section__body">
      <div class="list row">
        <div
          v-for="it in dataList"
          :key="it.name"
          :class="`list__item list__item__${it.itemIndex}`"
        >
          <div class="wrapper">
            <div class="name">{{ it.name }}</div>
            <div class="value">{{ it.value }}</div>
          </div>
          <div class="bg">
            <img :src="`/pages/dashboard/reports/p2_${it.itemIndex}.png`" />
          </div>
        </div>
      </div>
    </div>
  </BlockSection>
</template>

<script>
import { dashboardBlockMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardBlockMixin("masterDataRequest")],

  data() {
    return {};
  },

  computed: {
    innerTitle() {
      let prefix = "";

      if (this.isMonthly) {
        prefix = "本月";
      } else if (this.isAnnual) {
        prefix = "本年";
      }

      return `${prefix}主数据获取数量`;
    },

    dataList() {
      return (this.data.list || []).map((it, index) => {
        return { ...it, itemIndex: (index % 5) + 1 };
      });
    },

    chartOptions() {
      return {
        color: ["#a034d5", "#ae1937", "#c06e0e", "#c7ae17", "#1b8fad"],

        series: [
          {
            type: "pie",
            radius: [15, 45],
            center: ["50%", "50%"],
            itemStyle: { borderWidth: 1, borderColor: "#000000" },
            left: "5%",
            right: "5%",
            top: 0,
            bottom: 0,
            label: {
              alignTo: "edge",
              formatter: "{name|{b}}\n{percent|{d}%}",
              minMargin: 5,
              edgeDistance: 0,
              lineHeight: 15,
              rich: {
                name: {
                  fontSize: 14,
                  fontWeight: "bold",
                  color: "#fff",
                  width: 30,
                },
                percent: {
                  fontSize: 14,
                  fontWeight: "bold",
                  color: "#ffff00",
                  width: 50,
                },
                value: { fontSize: 14, fontWeight: "bold", color: "#33ccff" },
              },
            },
            labelLine: {
              length: 10,
              length2: 0,
            },
            labelLayout: (params) => {
              const points = params.labelLinePoints;
              return {
                labelLinePoints: points,
              };
            },
            data: this.data.list || [],
          },
        ],
      };
    },
  },
};
</script>

