<template>
  <div class="period-date-select selector">
    <div class="selector__field field">
      <div class="field__label">选择时间</div>
      <div class="field__input" @click="handleInputClick">
        <div class="field__text">
          <div v-if="isMonthly">{{ pageParams.month }}月</div>
          <div v-if="isAnnual">{{ pageParams.year }}年</div>

          <i class="icon ivu-icon ivu-icon-md-time"></i>
        </div>

        <div v-show="isGridVisible" class="selector__grid grid">
          <div
            class="grid__item"
            v-for="it in operiodOptions"
            :key="it"
            @click="handleGridItemClick(it)"
          >
            {{ it }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { dashboardMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardMixin],

  data() {
    return {
      isGridVisible: false,
    };
  },

  computed: {
    operiodOptions() {
      const startNum = this.isMonthly ? 1 : 2020;
      const count = 12;
      // const count = this.isMonthly ? 12 : dateUtil.year() - startNum

      return [...Array(count)].map((_, index) => startNum + index);
    },
  },

  mounted() {},
  beforeDestroy() {},

  methods: {
    handleInputClick() {
      this.isGridVisible = !this.isGridVisible;
    },

    handleGridItemClick(num) {
      const params = {};
      if (this.isMonthly) {
        params.month = num;
      } else {
        params.year = num;
      }

      this.loadData(params);
    },
  },
};
</script>

