<template>
  <BlockSection height="365px">
    <div class="section__header">
      <DataSectionHeader
        :title="innerTitle"
        :data="data.increment"
        height="75px"
      />
    </div>
    <div class="section__body">
      <ScrollListPeriod v-bind="innerListAttrs" height="220px" />
    </div>
  </BlockSection>
</template>

<script>
import { dashboardBlockMixin } from "@/components/dashboard/mixins/dashboardMixin.js";

export default {
  mixins: [dashboardBlockMixin("bizFinInterfaceAdd")],

  data() {
    return {};
  },

  computed: {
    innerTitle() {
      let prefix = "";

      if (this.isMonthly) {
        prefix = "本月";
      } else if (this.isAnnual) {
        prefix = "本年";
      }

      return `${prefix}新增接口`;
    },

    innerListAttrs() {
      return {
        columns: [
          { prop: "name", label: "接口名称", width: "190px" },
          { prop: "category", label: "业务分类", width: "130px" },
          { prop: "module", label: "功能模块", width: "130px" },
        ],
        data: this.data,
      };
    },
  },

  mounted() {},
  beforeDestroy() {},
};
</script>

<style lang="less" scoped>
.section {
  &__body {
    padding: 0 5px;
  }
}
</style>
